body.no_scroll {
	position: fixed;
	overflow: hidden !important;
}

body.no_scroll #main {
	width: 100vw;
	height: 100vh;
	overflow-y: scroll;
	-webkit-overflow-scrolling: touch;
}

button > span {
	position: relative;
}

.dimmed {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 2;
	background-color: rgba(0, 0, 0, 0.8);
}

.dimmed.type_munu {
	opacity: 0;
	visibility: hidden;
	transition: all ease-in-out .8s;
}

.dimmed.type_munu.on {
	opacity: 1;
	visibility: visible;
}

.dimmed.trans {
	background-color: transparent;
}

.meun_hambuger {
	display: inline-block;
	overflow: hidden;
	position: relative;
	width: 26px;
	height: 26px;
	vertical-align: top;
	text-align: right;
}

.meun_hambuger .center_line, .meun_hambuger:before, .meun_hambuger:after {
	display: block;
	height: 2px;
	background-color: #333;
	content: '';
	transition: all ease-in-out .3s;
	transform-origin: left;
}

.meun_hambuger .center_line {
	width: 20px;
	margin: 8px 0;
	margin-left: auto;
	vertical-align: top;
}

.meun_hambuger:before, .meun_hambuger:after {
	width: 120%;
}

.hidden_select {
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;
	width: 100%;
	opacity: 0;
}

@media all and (max-width: 1023px) {
	.meun_hambuger {
		width: 21px;
		height: 18px;
		padding: 6px;
		box-sizing: content-box;
	}
	.meun_hambuger:after, .meun_hambuger:before,
	.meun_hambuger .center_line {
		height: 1px;
	}
	.meun_hambuger .center_line {
		width: 16px;
		margin: 6px 0;
		margin-left: auto;
	}
	.meun_hambuger.type_mo_gnb {
		position: absolute;
		top: 50%;
		right: 9px;
		margin-top: -15px;
	}
	.meun_hambuger:before, .meun_hambuger:after {
		width: 100%;
	}
	.dimmed.type_munu {
		display: none;
	}
}

@media all and (max-width: 1200px) {
	.tablet_br {
		display: block;
	}
}

@media all and (min-width: 1024px) {
	.onlyPc {
		display: block !important;
	}
	.onlyMo {
		display: none !important;
	}
	.mobile_br {
		display: none !important;
	}
}

@media all and (max-width: 1023px) {
	.onlyPc {
		display: none !important;
	}
	.onlyMo {
		display: block !important;
	}
}

#header {
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	z-index: 50;
	text-align: center;
	transition: all ease-in-out .3s;
}

#header .logo {
	position: absolute;
	top: 33px;
	top: 2.063rem;
	left: 60px;
	left: 3.75rem;
}

#header .logo a {
	display: block;
	width: 166px;
	height: 43px;
