}

#header.on {
	background-color: #fff;
}

#header.on .logo a {
	background-image: url(../img/pc/common/logo_red.png);
}

#header.scroll {
	background-color: transparent;
}

#header.scroll .logo a {
	background-image: url(../img/pc/common/logo_red.png);
}

.sub #header.on:before {
	position: absolute;
	right: 0;
	left: 0;
	bottom: -1px;
	height: 1px;
	background-color: rgba(0, 0, 0, 0.1);
	content: '';
}

#gnb {
	position: relative;
	display: inline-block;
	z-index: 3;
}

.list_gnb {
	font-size: 0;
	transition: all ease-in-out .5s;
}

.list_gnb > li {
	position: relative;
	display: inline-block;
}

.list_gnb > li > a {
	position: relative;
	display: block;
	padding: 0 29px;
	padding: 0 1.813rem;
	color: #fff;
}

.list_gnb > li .name_gnb {
	position: relative;
	display: block;
	padding: 36px 0 37px;
	padding: 2.25rem 0 2.313rem;
	font-size: 18px;
	font-size: 1.125rem;
}

.list_gnb > li.on .name_gnb:after {
	position: absolute;
	right: 0;
	bottom: -1px;
	left: 0;
	z-index: 5;
	height: 2px;
	background-color: #ed1c23;
	content: '';
}

.list_gnb.on > li > a {
	color: #222;
}

.list_gnb.scroll {
	transform: translateX(150px);
	opacity: 0;
	visibility: hidden;
}

.gnb_depth {
	display: none;
	position: absolute;
	top: 100%;
	left: 29px;
	left: 1.813rem;
	z-index: 3;
	white-space: nowrap;
}

.gnb_depth > li {
	display: inline-block;
	font-size: 16px;
	font-size: 1rem;
}

.gnb_depth > li + li {
	margin-left: 50px;
	margin-left: 3.125rem;
}

.gnb_depth > li > a {
	display: block;
	padding: 17px 0;
	padding: 1.063rem 0;
	color: #666;
}

.gnb_depth > li > a:hover, .gnb_depth > li > a:focus {
	color: #ed1c23;
}

.gnb_depth.depth_last {
	right: 31px;
	right: 1.938rem;
	left: auto;
}

.depth_bg {
	position: absolute;
	top: 100%;
	right: 0;
	left: 0;
	height: 0;
	background-color: #fff;
	opacity: 0;
	transition: all ease-in-out .2s;
	box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.05);
}

.depth_bg:before {
	position: absolute;
	right: 0;
	left: 0;
	top: 0;
	height: 1px;
	background-color: rgba(0, 0, 0, 0.1);
	content: '';
}

.depth_bg.on {
	opacity: 1;
	height: 56px;
	height: 3.5rem;
}

